'use client';

import { useState } from 'react';
import TextInput from '@/components/TextInput';
import ControlPanel from '@/components/ControlPanel';
import ClusterVisualization from '@/components/ClusterVisualization';
import ClusterSummary from '@/components/ClusterSummary';
import { ClusterAlgorithm, TokenType, ClusterResult, ClusterRequest } from '@/lib/types';
import { Sparkles, BrainCircuit, Github } from 'lucide-react';

export default function Home() {
  const [text, setText] = useState('');
  const [algorithm, setAlgorithm] = useState<ClusterAlgorithm>('kmeans');
  const [numClusters, setNumClusters] = useState(5);
  const [tokenType, setTokenType] = useState<TokenType>('word');
  const [clusterResult, setClusterResult] = useState<ClusterResult | null>(null);
  const [isClustering, setIsClustering] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleCluster = async () => {
    if (!text.trim()) {
      setError('Please enter some text to cluster');
      return;
    }

    setIsClustering(true);
    setError(null);

    try {
      const request: ClusterRequest = {
        text,
        algorithm,
        numClusters,
        tokenType,
      };

      const response = await fetch('/api/cluster', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(request),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || 'Failed to cluster text');
      }

      setClusterResult(data);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred');
      console.error(err);
    } finally {
      setIsClustering(false);
    }
  };

  return (
    <main className="min-h-screen bg-background text-foreground bg-[radial-gradient(ellipse_at_top,_var(--tw-gradient-stops))] from-violet-900/20 via-background to-background dark overflow-x-hidden selection:bg-primary/30">
      <div className="fixed inset-0 bg-grid-pattern opacity-[0.03] pointer-events-none" />
      
      <div className="relative max-w-[1700px] mx-auto px-4 py-8 md:px-8 lg:py-8 flex flex-col min-h-screen">
        {/* Hero Header */}
        <header className="flex items-center justify-between mb-8 animate-fade-in relative z-10 shrink-0">
          <div className="flex items-center gap-4">
            <div className="p-3 bg-primary/10 rounded-2xl border border-primary/20 backdrop-blur-sm shadow-lg shadow-primary/10 group hover:scale-105 transition-transform duration-300">
              <BrainCircuit className="w-8 h-8 text-primary animate-pulse-glow" />
            </div>
            <div>
              <h1 className="text-3xl md:text-4xl font-bold tracking-tight">
                <span className="gradient-text">Neural</span>Clusters
              </h1>
              <p className="text-muted-foreground text-sm font-medium tracking-wide">
                AI-POWERED TEXT INTELLIGENCE
              </p>
            </div>
          </div>
          
          <a
            href="https://github.com"
            target="_blank"
            rel="noopener noreferrer"
            className="hidden md:flex items-center gap-2 px-4 py-2 rounded-full bg-secondary/50 hover:bg-secondary border border-border/50 hover:border-border transition-all hover:scale-105 group"
          >
            <Github className="w-5 h-5 group-hover:rotate-12 transition-transform" />
            <span className="text-sm font-medium">Star on GitHub</span>
          </a>
        </header>

        {/* Main Interface Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-12 gap-6 flex-1 relative z-10 min-h-0">
          
          {/* Left Panel: Inputs & Controls */}
          <div className="lg:col-span-4 space-y-6 flex flex-col min-h-0 animate-slide-up" style={{ animationDelay: '0.1s' }}>
            
            {/* Input Card */}
            <div className="glass-panel rounded-3xl p-1 flex-1 flex flex-col min-h-[300px]">
              <div className="bg-card/50 backdrop-blur-md rounded-[1.4rem] p-6 flex-1 flex flex-col h-full border border-white/5 relative group transition-all hover:bg-card/60">
                <TextInput 
                  text={text} 
                  setText={setText} 
                  isLoading={isClustering}
                />
              </div>
            </div>

            {/* Controls Card */}
            <div className="glass-panel rounded-3xl p-6 border border-white/10 shrink-0">
              <ControlPanel 
                algorithm={algorithm}
                setAlgorithm={setAlgorithm}
                numClusters={numClusters}
                setNumClusters={setNumClusters}
                tokenType={tokenType}
                setTokenType={setTokenType}
                onCluster={handleCluster}
                isClustering={isClustering}
              />
              {error && (
                <div className="mt-4 p-3 rounded-lg bg-red-500/10 border border-red-500/20 text-red-400 text-sm flex items-center gap-2 animate-shake">
                  <span className="w-1.5 h-1.5 rounded-full bg-red-500" />
                  {error}
                </div>
              )}
            </div>
          </div>

          {/* Right Panel: Visualization & Stats */}
          <div className="lg:col-span-8 flex flex-col gap-6 animate-slide-up min-h-0" style={{ animationDelay: '0.2s' }}>
            
            {/* Visualization Card */}
            <div className="glass-panel rounded-3xl p-1 flex-[2] min-h-[500px] relative overflow-hidden group">
              <div className="absolute inset-0 bg-gradient-to-br from-primary/5 via-transparent to-purple-500/5 opacity-0 group-hover:opacity-100 transition-opacity duration-700 pointer-events-none" />
              <div className="bg-card/30 backdrop-blur-sm rounded-[1.4rem] h-full w-full overflow-hidden border border-white/5 relative z-10 transition-all hover:bg-card/40">
                <ClusterVisualization data={clusterResult} />
              </div>
            </div>

            {/* Stats Card */}
            {clusterResult && (
              <div className="glass-panel rounded-3xl p-1 flex-1 animate-fade-in shrink-0">
                 <div className="bg-card/30 backdrop-blur-sm rounded-[1.4rem] p-6 h-full border border-white/5 relative overflow-hidden">
                   <div className="absolute top-0 right-0 w-32 h-32 bg-primary/5 rounded-full blur-3xl -mr-16 -mt-16 pointer-events-none" />
                  <ClusterSummary data={clusterResult} />
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Footer */}
        <footer className="mt-8 py-6 text-center text-sm text-muted-foreground/60 relative z-10 shrink-0">
          <p className="flex items-center justify-center gap-1">
            Engineered with <Sparkles className="w-3 h-3 text-primary animate-pulse" /> by NodeStudio Logic
          </p>
        </footer>
      </div>
    </main>
  );
}
