'use client';

import { useState } from 'react';
import { ClusterResult } from '@/lib/types';
import { VisualizationType, VISUALIZATION_OPTIONS } from '@/lib/visualization-types';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Network } from 'lucide-react';

// Import all visualization components
import dynamic from 'next/dynamic';

const ForceGraphVisualization = dynamic(() => import('./visualizations/ForceGraphVisualization'), {
  ssr: false,
});
const ScatterPlotVisualization = dynamic(() => import('./visualizations/ScatterPlotVisualization'), {
  ssr: false,
});
const WordCloudVisualization = dynamic(() => import('./visualizations/WordCloudVisualization'), {
  ssr: false,
});
const BarChartVisualization = dynamic(() => import('./visualizations/BarChartVisualization'), {
  ssr: false,
});
const BubbleChartVisualization = dynamic(() => import('./visualizations/BubbleChartVisualization'), {
  ssr: false,
});

interface ClusterVisualizationProps {
  data: ClusterResult | null;
}

export default function ClusterVisualization({ data }: ClusterVisualizationProps) {
  const [visualizationType, setVisualizationType] = useState<VisualizationType>('force-graph');

  if (!data) {
    return (
      <div className="flex flex-col items-center justify-center h-full min-h-[500px] bg-gradient-to-br from-gray-50 to-gray-100 dark:from-gray-900 dark:to-gray-800 rounded-xl border-2 border-dashed border-gray-300 dark:border-gray-600">
        <Network className="w-16 h-16 text-gray-400 dark:text-gray-500 mb-4" />
        <p className="text-gray-500 dark:text-gray-400 text-lg font-medium">
          No clustering data yet
        </p>
        <p className="text-gray-400 dark:text-gray-500 text-sm mt-2">
          Enter text and click "Start Clustering" to visualize
        </p>
      </div>
    );
  }

  const currentOption = VISUALIZATION_OPTIONS.find(opt => opt.value === visualizationType);

  return (
    <div className="relative h-full min-h-[500px]">
      {/* Visualization Type Selector */}
      <div className="absolute top-4 left-4 z-20 bg-white/95 dark:bg-gray-800/95 backdrop-blur-sm rounded-lg p-3 shadow-lg border border-gray-200 dark:border-gray-700">
        <div className="flex items-center gap-3">
          <span className="text-xs font-semibold text-gray-700 dark:text-gray-300">
            Visualization:
          </span>
          <Select value={visualizationType} onValueChange={(value) => setVisualizationType(value as VisualizationType)}>
            <SelectTrigger className="w-[220px] h-8 text-xs">
              <SelectValue>
                <span className="flex items-center gap-2">
                  <span>{currentOption?.icon}</span>
                  <span>{currentOption?.label}</span>
                </span>
              </SelectValue>
            </SelectTrigger>
            <SelectContent>
              {VISUALIZATION_OPTIONS.map(option => (
                <SelectItem key={option.value} value={option.value}>
                  <div className="flex items-center gap-2">
                    <span>{option.icon}</span>
                    <div>
                      <div className="font-medium">{option.label}</div>
                      <div className="text-xs text-gray-500">{option.description}</div>
                    </div>
                  </div>
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>

      {/* Render selected visualization */}
      <div className="w-full h-full">
        {visualizationType === 'force-graph' && <ForceGraphVisualization data={data} />}
        {visualizationType === 'scatter-plot' && <ScatterPlotVisualization data={data} />}
        {visualizationType === 'word-cloud' && <WordCloudVisualization data={data} />}
        {visualizationType === 'bar-chart' && <BarChartVisualization data={data} />}
        {visualizationType === 'bubble-chart' && <BubbleChartVisualization data={data} />}
      </div>
    </div>
  );
}
